import { Component, Input, OnInit } from '@angular/core';
import { LocalStorageServiceService } from 'src/service/local-storage-service.service';

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: ['./tabs.component.scss'],
})
export class TabsComponent implements OnInit {

  DatosUser: any

  constructor(
    private _localstorage: LocalStorageServiceService
  ) {
    //asigno los datos del usuario que tengo en el local storage 
    this.DatosUser = JSON.parse(this._localstorage.BuscarItem('DatosUser'))
  }

  ngOnInit() { }
}
